/**
 * Base
 */

.posts-wrap {
    @apply my-5 first:mt-0 last:mb-0 text-sm;
}

.post-wrap {
    @apply flow-root my-3 first:mt-0 last:mb-0;

    .entry-title,
    .entry-summary,
    .entry-meta {
        @apply my-2 first:mt-0 last:mb-0;
    }

    .entry-title {
        @apply text-[1.14em];
    }

    .entry-meta {
        @apply block text-[0.93em] text-gray-500;
    }

    > header,
    > footer {
        @apply my-3 first:mt-0 last:mb-0;
    }

    > header {
        @apply mt-0;
    }

    .more-link {
        @apply text-[0.93em];
    }
}

.big {
    > .post-wrap {
        @apply my-5 first:mt-0 last:mb-0;

        > header > .entry-title,
        > .entry-summary,
        > footer > .entry-meta,
        > header > .entry-meta {
            @apply my-3 first:mt-0 last:mb-0;
        }

        > header > .entry-title {
            @apply text-[1.43em] xl:text-[1.71em];
        }
    }
}
